/**
 * mockjsmock, mockAPI
 */
import Mock from 'mockjs'
import data from './data.json'
console.log(data)
// 
Mock.mock('/api2/goods', {
  code: 0,
  data: data.goods
})
Mock.mock('/api2/ratings', {
  code: 0,
  data: data.ratings
})
Mock.mock('/api2/seller', {
  code: 0,
  data: data.seller
})



